export const createMenuOptions = (
    {
        save_subject = false,
        download = false,
        create_image = false,
        increase_quality = false,
        remove_subject_background = false,
        create_video = false,
        on_save_subject = () => null,
        on_download = () => null,
        on_create_image = () => null,
        on_increase_quality = () => null,
        on_remove_subject_background = () => null,
        on_create_video = () => null,
    }
) => {
    return [
        ...save_subject ? [
            {
                label: 'Lưu đối tượng',
                icon: 'solar:gallery-wide-linear',
                onClick: on_save_subject
            }
        ] : [],
        ...download ? [
            {
                label: 'Tải xuống',
                icon: 'solar:cloud-download-linear',
                onClick: on_download
            }
        ] : [],
        ...create_image ? [
            {
                label: 'Tạo ảnh',
                icon: 'solar:gallery-add-broken',
                onClick: on_create_image
            }
        ] : [],
        ...increase_quality ? [
            {
                label: 'Tăng chất lượng',
                icon: 'solar:high-quality-broken',
                onClick: on_increase_quality
            }
        ] : [],
        ...remove_subject_background ? [
            {
                label: 'Xoá nền đối tượng',
                icon: 'solar:file-remove-broken',
                onClick: on_remove_subject_background
            }
        ] : [],
        ...create_video ? [
            {
                label: 'Tạo Video',
                icon: 'solar:videocamera-add-broken',
                onClick: on_create_video
            }
        ] : [],
    ];
}

