export class ServiceFetchBase {
    constructor({service_port, device_id = null}) {
        this.service_port = service_port;
        this.base_url = `http://localhost:${this.service_port}/api/v1`;
        this.device_id = device_id;
    }

    async getJson({url_path, params = {},method = 'GET'}) {
        const url = new URL(this.base_url + url_path);
        Object.keys(params).forEach(key => url.searchParams.append(key, params[key]));
        let resp = await fetch(url,{
            method: method || 'GET'
        });
        return await resp.json();
    }

    async postJson({url_path, body = {}}) {
        const url = new URL(this.base_url + url_path);
        let resp = await fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(body)
        });
        return await resp.json();
    }

    getCameras = () => this.getJson({url_path: '/cameras'});
    getLUTs = () => this.getJson({url_path: '/luts'});
    // /api/v1/luts/upload'
    uploadLUT = async ({file, name}) => {
        const url = new URL(this.base_url + '/luts/upload');
        const formData = new FormData();
        formData.append('file', file);
        formData.append('name', name);
        let resp = await fetch(url, {
            method: 'POST',
            body: formData
        });
        return await resp.json();

    }

    // /api/v1/luts/{filename}
    async deleteLUT(filename) {
        let encodedFilename = encodeURIComponent(filename);
        let resp = await fetch(this.base_url + `/luts/${encodedFilename}`, {
            method: 'DELETE'
        });
        return await resp.json();
    }

    getConfig = () => this.getJson({url_path: '/config'})
    updateConfig = (newConfig) => this.postJson({url_path: '/config', body: newConfig});
    getCameraStreamUrl = (unique_id, params = {}) => {
        const url = new URL(this.base_url + `/cameras/${unique_id}`);
        Object.keys(params).forEach(key => url.searchParams.append(key, params[key]));
        return url.toString();
    }
    getCameraSnapshotUrl = (unique_id, params = {}) => {
        const url = new URL(this.base_url + `/cameras/snapshot`);
        Object.keys(params).forEach(key => url.searchParams.append(key, params[key]));
        return url.toString();
    }
    stopCamera = (unique_id) => this.getJson({url_path: `/cameras/stream`, method: 'DELETE'});

//     post /api/v1/tuya
    remoteTuyaCommand = ({action}) => this.postJson({url_path: '/tuya', body: {action}});

    getBanks = () => this.getJson({url_path: '/banks'});
//     /api/v1/printers
    getPrinters = () => this.getJson({url_path: '/printers'});
}

