<template>
  <div :class="{
    'model-content__wrapper':true,
    'no-padding':no_padding,
    'fixed':header_fixed,
  }"
       :style="{
    width,
  }"
  >
    <div :class="{'modal-header':true,'no-padding':no_padding}">
      <template v-if="is_custom_header">
        <slot name="header"></slot>
      </template>
      <template v-else>
        <n-flex align="center" justify="space-between">
          <div class="flex-3 title" style="flex: 1">{{ title }}</div>
          <button
              class="btn-header-color"
              @click="$emit('close')"
          >
            <Icon icon="solar:close-circle-bold" height="20"></Icon>
          </button>
        </n-flex>
      </template>
    </div>
    <div class="modal-content">
      <n-infinite-scroll :style="{maxHeight: max_height}">
        <div class="inner-scroll">
          <slot name="default"></slot>
        </div>
      </n-infinite-scroll>
    </div>
    <div class="modal-footer text-right">
      <template v-if="is_custom_footer">
        <slot name="footer"></slot>
      </template>
      <template v-else>
        <div class="text-right">
          <n-button
              v-if="show_bottom_back_button"
              @click="$emit('close')" type="default"
              round
          >
            Đóng
          </n-button>
          &nbsp;
          <template v-if="!action_hide">
            <n-button
                round
                :type="action_type"
                :disabled="action_disabled || action_loading"
                @click="emitAction"
            >
              <template v-if="action_loading">
                <n-spin stroke="white" :size="12"></n-spin>
                &nbsp;
              </template>
              {{ action_text }}
            </n-button>
          </template>
        </div>
      </template>
    </div>
  </div>
</template>
<script>
import {Icon} from "@iconify/vue";

export default {
  name: 'component__modal-content',
  components: {Icon},
  props: {
    title: {
      type: String,
      default: ''
    },
    width: {
      type: String,
      default: '500px'
    },
    no_padding: {
      type: Boolean,
      default: false
    },
    hide_header: {
      type: Boolean,
      default: false
    },
    is_custom_header: {
      type: Boolean,
      default: false
    },
    hide_footer: {
      type: Boolean,
      default: false
    },
    is_custom_footer: {
      type: Boolean,
      default: false
    },
    action_loading: {
      type: Boolean,
      default: false
    },
    action_type: {
      type: String,
      default: 'primary'
    },
    action_text: {
      type: String,
      default: 'Lưu thông tin'
    },
    action_disabled: {
      type: Boolean,
      default: false
    },
    action_hide:{
      type: Boolean,
      default: false
    },
    max_height: {
      type: String,
      default: 'calc(100vh - 180px)'
    },
    header_fixed: {
      type: Boolean,
      default: false
    },
    close_on_click_outside: {
      type: Boolean,
      default: true
    },
    show_bottom_back_button: {
      type: Boolean,
      default: true
    }
  },

  data() {
    return {}
  },
  methods: {
    emitAction() {
      this.$emit('action');
    }
  }
}
</script>
