<template>
  <n-scrollbar class="background-lib">
    <n-flex justify="center">
      <div style="max-width:calc(100vh - 32px); min-width:200px; padding:32px;">
        <template v-if="image_url.length===0">
          <n-flex class="label">
            Đang tải thư viện...
          </n-flex>
          <div style="width:100%;">
            <n-row :gutter="12">
              <template v-for="e in Array.from({length:8})">
                <n-col :span="8">
                  <n-skeleton style="margin-top:12px; height: 160px; border-radius: 4px"/>
                </n-col>
              </template>
            </n-row>

          </div>
        </template>
        <template v-else>
          <n-flex class="label">
           Lúc {{new Date(info.created_at * 1000).toLocaleString()}}
          </n-flex>
          <n-row :gutter="12">
            <template v-for="(e,index) in [...image_url]">
              <n-col :span="8" >
                <n-image :src="e"
                         style="margin-top:6px;   overflow:  hidden; aspect-ratio: 3/4"/>
              </n-col>
            </template>
          </n-row>
        </template>
      </div>
    </n-flex>
  </n-scrollbar>
</template>
<script>
export default {
  name: 'lib',
  data() {
    return {
      is_loading: true,
      image_url: [],
      info: {},
      is_failed: false,
    }
  },
  beforeMount() {
    this.getInfo();
  },
  methods: {
    async getInfo() {
      let url = `https://zhikong.ai/api/v1/open-api/anonymous/library?` + new URLSearchParams({
        ...this.$route.query || {}
      }).toString();
      this.is_loading = true;
      let dt = await fetch(url);
      try {
        let res = await dt.json();
        this.image_url = (res?.data?.image_ids || []).map(e => {
          return 'https://zhikong.ai/api/v1/cdn/' + encodeURIComponent(e);
        });
        this.info = res?.data || {};
        this.is_loading = false;
      } catch (e) {
        console.log('e');
      }
    }
  }
}
</script>
