<template>
  <router-view/>
</template>
<script>
import {$AF} from "@/global";
import {useMessage, useNotification, useDialog, useLoadingBar} from "naive-ui";

export default {
  name: 'App',
  data() {
    return {};
  },
  computed: {},
  beforeMount() {
    $AF.$msg = useMessage();
    $AF.$ntf = useNotification();
    $AF.$question = useDialog();
    $AF.$loading = useLoadingBar();
  },
  mounted() {
    window.addEventListener('keyup', e => {
      if (e.key === 'F9' && e.ctrlKey) {
        this.$router.push({path: '/setting',query: this.$route.query || {} });
      }
    });
  },
  beforeDestroy() {
  },
  methods: {}
}
</script>
