import {NButton, NFlex} from "naive-ui";
import {h} from "vue";
import {Icon} from "@iconify/vue";
import router from "@/router";
import {SERVER_URL} from "@/util";

export var $AF = {
    $ntf: {},
    $loading: {},
    $msg: {},
    $question: {},
    $picker: ({min, max, blob}) => {
        return new Promise(resolve => {
        })
    },
    $cropper: () => {
    },
    $appFunc: {
        getImageBase64: async url => {
            const response = await fetch(url);
            const blob = await response.blob();
            return new Promise((resolve, reject) => {
                const reader = new FileReader();
                reader.readAsDataURL(blob);
                reader.onloadend = () => resolve(reader.result);
                reader.onerror = reject;
            });
        },
        getImageBase64Loading: url => {
            return new Promise(async resolve => {
                $AF.$loading.start();
                let b64 = await $AF.$appFunc.getImageBase64(imageCDN(url));
                $AF.$loading.finish();
                if (b64) {
                    resolve(b64);
                }
            })
        },
        registrationProfileCreateOrUpdate: ({base,callback}) => {},
    },
    $startDownload: (
        {
            file_url,
            file_name,
            file_ex = 'png',
            download_callback,
            mime_type,
            is_get_blob_only = false,
        }) => {
    },
    $loginRequire: () => {

    },
    $creationView: ({creation_id, creation}) => null,
    $imageView: imageUrl => ({}),
    videoCutter: ({videoUrl}) => new Promise(resolve => {
    }),
    $fontPicker: ({defaultFont}) => new Promise(resolve => {
    }),
};

$AF.$qConfirm = msg => {
    return new Promise(resolve => {
        $AF.$question.info({
            title: 'Xác nhận',
            content: msg || 'Bạn có chắc chắc chắn không?',
            positiveText: 'Chắc chắn',
            negativeText: 'Không',
            draggable: true,
            showIcon: false,
            onPositiveClick: () => {
                resolve(true);
            },
            onNegativeClick: () => {
                resolve(null);
            },
            positiveButtonProps: {
                type: 'primary',
            }
        })
    });
}
$AF.$handleError = (error) => {
    return new Promise(resolve => {
        if (!error) {
            resolve();
        } else {
            let error_text = error;
            if (typeof error === 'object') {
                error_text = JSON.stringify(error);
            }
            $AF.$msg.error({
                title: 'Lỗi',
                content: error_text,
            });
        }

    });
};
$AF.$handleResponseError = async (response) => {
    return new Promise(resolve => {
        if (response?.error === 0) {
            resolve();
        } else {
            let result = response.message || 'Lỗi không xác định';
            if (result === 'system_error') {
                result = 'Máy chủ gặp sự cố';
            }
            $AF.$msg.error(result);
        }
    });
};
$AF.$lockErrorResponse = async ({error, result}) => {
    return new Promise((resolve) => {
        (async () => {
            await $AF.$handleError(error);
            await $AF.$handleResponseError(result);
            resolve();
        })();
    });
}
$AF.$msgSuccess = (result) => {
    let msg = result && result.message || result || 'Success';
    $AF.$msg.success(msg);
}
$AF.$msgWarning = (result) => {
    let msg = result && result.message || result || 'Warning';
    $AF.$msg.warning(msg);
}
$AF.$uploadFileThread = ({ref_list, on_upload_error}) => {
    return new Promise(resolve => {
        (async () => {
            let temp = await Promise.all([...ref_list.filter(e => e.upload).map(e => e.upload())]);
            let error = temp.find(e => typeof e !== 'string' && (e.error || e.result.error));
            if (error) {
                on_upload_error && on_upload_error(error);
            } else {
                resolve();
            }
        })();
    });
}
$AF.$sortData = data => {
    if (data.length > 0 && typeof data[0].sort === 'number') {
        data = data.sort((a, b) => a.sort - b.sort);
    }
    return data;
}
$AF.$filter = {
    $currency: (value) => (value || 0).toLocaleString('vi-VN'),
}

export const tableSingleButton = ({text, strong, round, ghost, row, type, size, tertiary, onClick}) => {
    return h(
        NButton,
        {
            strong: strong || false,
            type: type || 'default',
            tertiary,
            ghost,
            size: size || 'small',
            round: round || false,
            onClick: () => onClick(row)
        },
        {default: () => text}
    );
}

export const tableMultiButton = (options) => {
    return h(
        NFlex,
        {
            wrap: false,
            justify: 'end',
        },
        options.map(e => {
            return h(
                NButton,
                {
                    size: 'small',
                    onClick: () => e.onClick(),
                    ghost: e.ghost || false,
                    type: e.type || 'default',
                },
                {default: () => e.text}
            );
        })
    )
}

export const tableRenderCheckedIcon = (status) => {
    if (!status) {
        return h('');
    }
    return h(
        Icon,
        {
            icon: 'solar:check-circle-bold',
            color: '#00a854',
            height: '16',
        }
    );
}

export const tableRenderCurrency = number => {
    return h('span', {}, (number || 0).toLocaleString('vi-VN', {style: 'currency', currency: 'VND'}));
}

export const tableRenderNumber = number => {
    return h('span', {}, (number || 0).toLocaleString('vi-VN'));
}


export const tableRenderText = text => {
    return h('span', {}, text);
}
export const tableRenderDateFromTimestamp = timestamp => {
    return tableRenderText(new Date(timestamp * 1000).toLocaleString());
}
export const AI_MODEL_TASK = {
    TEXT: 0,
    TEXT_TO_IMAGE: 1,
    TEXT_TO_VIDEO: 2,
    IMAGE_TO_IMAGE: 3,
    IMAGE_TO_VIDEO: 4,
    VIDEO_TO_VIDEO: 5,
    UPSCALE_IMAGE: 6,
    UPSCALE_VIDEO: 7,
    TEXT_TO_SPEECH: 8,
    IMAGE_TO_TEXT: 9,
    TEMPLATE_TO_VIDEO: 10,
};


export const imageCDN = file_path => {
    if (!file_path) return file_path;
    file_path = file_path.toString();
    if (file_path.startsWith('http') || file_path.startsWith('blob') || file_path.startsWith('data')) {
        return file_path;
    }
    return SERVER_URL + '/v1/cdn/' + encodeURIComponent(file_path);
}
export const menuOptionsFunctions = {
    save_subject: async ({url, b64}) => {
        if (url && !b64) {
            b64 = await $AF.$appFunc.getImageBase64Loading(url);
        }
        if (url && b64) {
            $AF.$appFunc.subjectManagerSave({
                url, b64
            })
        }
    },
    download: ({url}) => {
        $AF.$startDownload({
            file_url: url,
            file_name: 'Downloading'
        });
    },
    redirect: ({url, path}) => router.push(`${path}?image=${encodeURIComponent(url)}`),
    create_image: ({url}) => menuOptionsFunctions.redirect({url, path: '/creation/image'}),
    image_quality: ({url}) => menuOptionsFunctions.redirect({url, path: '/creation/image/quality'}),
    image_remove_bg: ({url}) => menuOptionsFunctions.redirect({url, path: '/creation/image/rm-bg'}),
    create_video: ({url}) => menuOptionsFunctions.redirect({url, path: '/creation/video/from-image'}),
}
