<template>
  <div class="native-input-quantity">
    <n-input-number v-model:value="scopeValue"
                    :step="step"
                    :size="size" :parse="parse" :format="format"/>
  </div>
</template>
<script>
export default {
  name: 'component__input-number',
  props: {
    modelValue: {
      type: Number,
      default: 1,
    },
    max: {
      type: Number,
      default: 10000,
    },
    min: {
      type: Number,
      default: 1,
    },
    size: {
      type: String,
      default: 'medium',
    },
    step: {
      type: Number,
      default: 1,
    },
  },
  data() {
    return {
      scopeValue: 0,
      parse: (input) => {
        const nums = input.replace(/,/g, '').trim()
        if (/^\d+(\.(\d+)?)?$/.test(nums))
          return Number(nums)
        return nums === '' ? null : Number.NaN
      },
      format: (value) => {
        if (value === null)
          return ''
        return value.toLocaleString('en-US')
      },
    }
  },
  watch: {
    modelValue: function (val) {
      if (val !== this.scopeValue) {
        this.scopeValue = val;
      }
    },
    scopeValue: function (val) {
      if (val !== this.modelValue) {
        let value = val;
        if (val > this.max) {
          value = this.max;
        }
        if (val < this.min) {
          value = this.min;
        }
        if (value !== val) {
          this.scopeValue = value;
        }

        this.$emit('update:modelValue', value);
      }
    }
  },
  beforeMount() {
    this.scopeValue = this.modelValue;
  },
  methods: {
    sub() {
      let value = this.scopeValue - 1;
      if (value < this.min) {
        value = this.min;
      }
      this.scopeValue = value;
      if (this.scopeValue !== this.modelValue) {
        this.$emit('update:modelValue', value);
      }
    },
    add() {
      let value = this.scopeValue + 1;
      if (value > this.max) {
        value = this.max;
      }
      this.scopeValue = value;
      if (this.scopeValue !== this.modelValue) {
        this.$emit('update:modelValue', value);
      }
    }
  }
}
</script>