<style lang="scss">
@import '@/global';

.font-item {
  border-radius: 12px;
  margin-bottom: 8px;
  border: 1px solid rgba(45, 45, 48, .1);
  overflow: hidden;
  cursor: pointer;

  img {
    border-radius: 8px;
  }

  .font-item__name {
    padding-bottom: 10px;
    font-weight: 500;
    font-size: 13px;
  }

  &:hover, &.active {
    border: 1px solid $primary-color;

    .font-item__name {
      color: $primary-color;
    }
  }
}
</style>
<template>
  <div class="font-picker">
    <n-modal v-model:show="show">
      <component__modal-content
          title="Chọn Font chữ" width="600px"
          @close="show = false"
          :action_text="`Chọn (${selected_font})`"
          @action="confirmSelectFont"
      >
        <n-flex>
          <n-input
              style="max-width: 300px"
              placeholder="Tìm kiếm font chữ ..."
              v-model:value="keyword"
          >
            <template #prefix>
              <Icon icon="solar:magnifer-outline"></Icon>
            </template>
          </n-input>
        </n-flex>
        <n-scrollbar style="margin-top:12px; max-height: calc(100vh - 300px); min-height:100px;">
          <component_empty v-if="_filtered_font_list.length===0"></component_empty>
          <n-row :gutter="12">
            <template v-for="e in _filtered_font_list">
              <n-col :span="8">
                <div
                    @click="selected_font=e.name"
                    :class="{
                  'font-item':true,
                  'active': selected_font === e.name,
                }">
                  <div>
                    <img :src="getImage(e)" alt="">
                  </div>
                  <n-flex justify="center" class="font-item__name">
                    <span>{{ e.name }}</span>
                  </n-flex>
                </div>
              </n-col>
            </template>
          </n-row>
        </n-scrollbar>
      </component__modal-content>
    </n-modal>
  </div>
</template>
<script>
import Component__modalContent from "@/components/component_modal_content.vue";
import Component_empty from "@/components/component__empty.vue";
import {SERVER_URL} from "@/util";

export default {
  name: 'component__font-picker',
  components: {Component_empty, Component__modalContent},
  data() {
    return {
      font_list: [],
      selected_font: '',
      show: false,
      keyword: '',
      callback: null,
      SERVER_URL,
    }
  },
  computed: {
    _filtered_font_list() {
      let keyword = this.keyword.toLowerCase().trim();
      return this.font_list.filter(e => {
        return e.keyword.includes(keyword);
      });
    }
  },
  watch: {
    show: function (val) {
      if (val && this.font_list.length === 0) {
        this.getFontList();
      }
    }
  },
  beforeMount() {
    this.$AF.$fontPicker = ({defaultFont}) => {
      return new Promise(resolve => {
        this.selected_font = defaultFont || this.selected_font;
        this.callback = resolve;
        this.show = true;
      });
    }
  },
  methods: {
    confirmSelectFont(){
      this.callback && this.callback(this.selected_font);
      this.show = false;
    },
    getImage(e) {
      return [SERVER_URL.replace('/api', ''), 'font_samples', e.image].join('/');
    },
    async getFontList() {
      let result = await (await this.$fetch.base.getFonts()).json();
      this.font_list = (result || []).map(e => {
        return {
          ...e,
          keyword: e.name.toLowerCase(),
        }
      });
    }
  }
}
</script>