<template>
  <div :class="{
    'fn__select_model':true,
    'opened':is_open
  }"
  >
    <a class="model_open"
       @click="handleTrigger('click')" @mouseover="handleTrigger('over')">
      <slot name="ref"></slot>
    </a>
    <div class="all_models">
      <slot></slot>
    </div>
  </div>
</template>
<script>
export default {
  name: 'component__popover',
  props: {
    trigger: {
      type: String,
      default: 'click'
    },
  },
  data() {
    return {
      is_open: false,
    }
  },
  methods: {
    handleTrigger(type = 'click') {
      if (type !== this.trigger) return 0;
      this.is_open = !this.is_open;
    }
  }
}
</script>
<style lang="scss">
.all_models {
  background-color: var(--atosa-site-bg-color);
  border-radius: 6px;
}
</style>