<template>
  <n-popover
      :show="download_list.length>0"
      trigger="manual"
      placement="top-start"
  >
    <template #trigger>
      <div class="download-gui">
      </div>
    </template>
    <div class="download-gui__container">
      <n-scrollbar style="max-height:200px; min-height:40px;">
        <template v-for="e in download_list" :key="e.file_url">
          <n-flex class="file-item" align="center">
            <n-progress type="circle" :percentage="e.percent"
                        style="width: 20px;"
                        :color="['red','grey','blue','green'][e.status+1]"
            >
              <template #default></template>
            </n-progress>
            <div class="flex-1">
              <div class="file-name">{{ e.file_name }}</div>
              <div class="file-path">{{ srcCDN(e.file_url).substr(0, 36) }}...</div>
            </div>
          </n-flex>
        </template>
      </n-scrollbar>
    </div>
  </n-popover>

</template>
<script>
export default {
  name: 'component__download_gui',
  data() {
    return {
      show: true,
      download_list: [],
      interval_check_download: null,
      maximum_thread: 4,
    }
  },
  beforeMount() {
    this.$AF.$startDownload = (
        {
          file_url,
          file_name,
          file_ex = 'png',
          download_callback,
          mime_type,
          is_get_blob_only = false,
        }) => {
      let downloading_url = this.download_list.map(e => e.file_url);
      // if (downloading_url.includes(file_url)) return this.$AF.$msgWarning('Tệp đang trong danh sách tải xuống!');
      this.download_list.push({
        file_name, file_url, file_ex,
        start: new Date(),
        status: 0,
        percent: 0,
        download_callback,
        mime_type,
        is_get_blob_only,
      });
    }
    this.interval_check_download = setInterval(() => {
      let downloading = this.download_list.filter(e => e.status === 0);
      if (downloading > this.maximum_thread) return;
      let find = downloading.find(e => e.status === 0);
      if (find) this.downloadFile(find);
    }, 1000);

  },
  methods: {
    async downloadFile(file_config) {
      file_config.status = 1;
      try {
        let url = this.srcCDN(file_config.file_url);
        const response = await fetch(url);
        const reader = response.body.getReader();

        const contentLength = +response.headers.get('Content-Length');
        let contentType = '';
        if (!contentType) {
          for (; !contentType;) {
            if (url.endsWith('.mp4')) {
              contentType = 'video/mp4';
            } else if (url.endsWith('.m4v')) {
              contentType = 'video/mp4';
            } else if (url.endsWith('.mp3')) {
              contentType = 'audio/mpeg';
            } else if (url.endsWith('.wav')) {
              contentType = 'audio/wav';
            } else if (url.endsWith('.ogg')) {
              contentType = 'audio/ogg';
            } else if (url.endsWith('.webm')) {
              contentType = 'video/webm';
            } else if (url.endsWith('.gif')) {
              contentType = 'image/gif';
            } else if (url.endsWith('.png')) {
              contentType = 'image/png';
            } else if (url.endsWith('.jpg')) {
              contentType = 'image/jpeg';
            } else if (url.endsWith('.jpeg')) {
              contentType = 'image/jpeg';
            } else if (url.endsWith('.json')) {
              contentType = 'application/json';
            } else if (url.endsWith('.doc')) {
              contentType = 'application/msword';
            } else if (url.endsWith('.docx')) {
              contentType = 'application/vnd.openxmlformats-officedocument.wordprocessingml.document';
            } else if (url.endsWith('.xls')) {
              contentType = 'application/vnd.ms-excel';
            } else if (url.endsWith('.xlsx')) {
              contentType = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet';
            } else if (url.endsWith('.pdf')) {
              contentType = 'application/pdf';
            } else if (url.endsWith('.zip')) {
              contentType = 'application/zip';
            } else if (url.endsWith('.rar')) {
              contentType = 'application/x-rar-compressed';
            } else {
              contentType = 'application/octet-stream';
            }
            break;
          }
        }
        // content-disposition

        let receivedLength = 0;
        let chunks = [];

        for (; ;) {
          const {done, value} = await reader.read();
          if (done) break;
          chunks.push(value);
          receivedLength += value.length;
          file_config.percent = Math.floor(((receivedLength / contentLength) * 100));
        }

        let blob = new Blob(chunks, {type: contentType,name: file_config.file_name || 'downloaded_file'});
        blob.name = file_config.file_name || 'downloaded_file';
        const blobUrl = URL.createObjectURL(blob);
        if (file_config.is_get_blob_only) {
          file_config.download_callback && file_config.download_callback(blob);
          file_config.status = 2;

        } else {
          const a = document.createElement('a');
          a.href = blobUrl;

          let filename = file_config.file_name || url.split('/').pop().split('?')[0];
          if (!filename || !filename.includes('.')) {
            const extension = file_config.file_ex || (contentType ? contentType.split('/')[1] : 'bin');
            filename = `iPOSInvoice-file.${extension}`;
          }
          a.download = filename;
          document.body.appendChild(a);
          a.click();
          a.remove();
          URL.revokeObjectURL(blobUrl);
          file_config.status = 2;
        }
      } catch (e) {
        file_config.status = -1;
      }
      let index = this.download_list.map(e => e.file_url).indexOf(file_config.file_url);
      this.download_list.splice(index, 1);
    }
  }
}
</script>
<style lang="scss">
.download-gui {
  position: fixed;
  z-index: 2;
  bottom: 0;
  left: 10px;

}

.download-gui__container {
  width: 300px;

  .file-item {
    padding: 10px 12px;
    border-bottom: 1px solid rgba(45, 45, 48, .2);

    &:last-child {
      border-bottom: none;
    }
  }

  .file-name {
    font-size: 14px;
  }

  .file-path {
    font-size: 12px;
    color: grey;
  }
}
</style>