<template>
  <div class="input-text-auto">
    <textarea :id="el_id" rows="1" @keyup="calculateHeight" v-model="scopeValue" @mouseup="calculateHeight"></textarea>
    <textarea :value="scopeValue" :class="'fn__hidden_textarea fn_hide_'+el_id" rows="1"
              tabindex="-1"></textarea>
  </div>
</template>
<script>

export default {
  name: 'component__input-text',
  props: {
    modelValue: {
      type: String,
      default: () => '222',
    }
  },
  data() {
    return {
      scopeValue: '1',
      el_id: '',
    }
  },
  watch: {
    modelValue: function (val) {
      if (val !== this.scopeValue) {
        this.scopeValue = val;
      }
    },
    scopeValue: function (val) {
      this.$emit('update:modelValue', val);
    }
  },
  beforeMount() {
    this.el_id = `input_text_${this.generateCode(24)}`;
    this.scopeValue = this.modelValue;
  },
  mounted() {
    this.calculateHeight();
  },
  methods: {
    calculateHeight() {
      let e = this.jquery('#' + this.el_id);
      let padding = 34;
      let border = 4;
      let taLineHeight = 22;
      let e2 = this.jquery('.fn_hide_' + this.el_id)[0];
      let taHeight2 = e2.scrollHeight - padding;
      let numberOfLines2 = Math.floor(taHeight2 / taLineHeight);
      e.css({height: numberOfLines2 * taLineHeight + padding + border});
    }
  }
}

</script>