import {createApp} from 'vue';
import AppProvider from './provider_wrapper.vue';
import './registerServiceWorker'
import router from './router'
import store from './store'
import jQuery from 'jquery';
import {Icon} from "@iconify/vue";

import componentFilePick from './components/component_file_picker.vue';
import componentImageCropper from './components/component_image_cropper.vue';
import componentInputNumber from './components/component_input_number.vue';
import componentInputText from './components/component_input_text.vue';
import componentSwitch from './components/component_switch.vue';
import componentPopover from './components/component_popover.vue';
import componentSelectSingle from './components/component_select_single.vue';
import componentSidebarPageTrigger from './components/component_sidebar_page_trigger.vue';
import componentSlider from './components/component_slider.vue';
import componentTooltip from './components/component_tooltip.vue';
import naiveUI from 'naive-ui';
import {$AF, imageCDN, menuOptionsFunctions} from "@/global";
import 'vue-advanced-cropper/dist/style.css';
import {createMenuOptions, INPUT_FILE_TYPE} from "@/util";
import {ServiceFetchBase} from "@/services/fetch/service_fetch_base";


export const vueApp = createApp(AppProvider);
let $fetch = {
    base: new ServiceFetchBase({service_port: 5000})
};


vueApp
    .component('component__file-picker', componentFilePick)
    .component('component__image-cropper', componentImageCropper)
    .component('component__input-number', componentInputNumber)
    .component('component__input-text', componentInputText)
    .component('component__switch', componentSwitch)
    .component('component__popover', componentPopover)
    .component('component__select-single', componentSelectSingle)
    .component('component__sidebar-page-trigger', componentSidebarPageTrigger)
    .component('component__slider', componentSlider)
    .component('component__tooltip', componentTooltip)
    .component('Icon', Icon);
vueApp.config.globalProperties.$fetch = $fetch;
vueApp.config.globalProperties.jquery = jQuery;
vueApp.config.globalProperties.timestamp = e => Math.floor(new Date(e).getTime() / 1000);
vueApp.config.globalProperties.generateCode = (length) => {
    let result = '';
    let characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    let charactersLength = characters.length;
    for (let i = 0; i < length; i++) {
        result += characters.charAt(Math.floor(Math.random() *
            charactersLength));
    }
    return result;
};
vueApp.config.globalProperties.sleep = time => {
    return new Promise(resolve => {
        setTimeout(() => {
            resolve();
        }, time);
    });
};
vueApp.config.globalProperties.$AF = $AF;
vueApp.config.globalProperties.unbindObj = e => JSON.parse(JSON.stringify(e));
vueApp.config.globalProperties.overrideObj = (obj, _newObj) => {
    let newObj = vueApp.config.globalProperties.unbindObj(_newObj);
    Object.keys(newObj).forEach(key => {
        if (obj[key] !== newObj[key]) {
            obj[key] = newObj[key];
        }
    });
}
vueApp.config.globalProperties.removeUnicode = (value) => {
    if (typeof value === 'undefined') {
        return ''
    }
    value = value
        .toString()
        .toLowerCase()
    value = value.replace(/à|á|ạ|ả|ã|â|ầ|ấ|ậ|ẩ|ẫ|ă|ằ|ắ|ặ|ẳ|ẵ/g, 'a')
    value = value.replace(/è|é|ẹ|ẻ|ẽ|ê|ề|ế|ệ|ể|ễ/g, 'e')
    value = value.replace(/ì|í|ị|ỉ|ĩ/g, 'i')
    value = value.replace(/ò|ó|ọ|ỏ|õ|ô|ồ|ố|ộ|ổ|ỗ|ơ|ờ|ớ|ợ|ở|ỡ/g, 'o')
    value = value.replace(/ù|ú|ụ|ủ|ũ|ư|ừ|ứ|ự|ử|ữ/g, 'u')
    value = value.replace(/ỳ|ý|ỵ|ỷ|ỹ/g, 'y')
    value = value.replace(/đ/g, 'd')
    return value
}
vueApp.config.globalProperties.srcCDN = imageCDN;
vueApp.config.globalProperties.imageCDN = imageCDN;
vueApp.config.globalProperties.INPUT_FILE_TYPE = INPUT_FILE_TYPE;
vueApp.config.globalProperties.createMenuOptions = createMenuOptions;
vueApp.config.globalProperties.menuOptionsFunctions = menuOptionsFunctions;
vueApp.use(store).use(router).use(naiveUI).mount('#app');
