<template>
  <div class="file-picker">
    <input type="file"
           :id="id"
           ref="file_picker"
           :accept="accept"
           :multiple="max>1"
           @change="handleFileChange"
    />
  </div>
</template>
<script>
import {$AF} from "@/global";
import {handleError, useTemplateRef} from "vue";
import {INPUT_FILE_TYPE} from "@/util";

export default {
  name: 'component__file-picker',
  data() {
    return {
      accept: INPUT_FILE_TYPE.IMAGE,
      max: 1,
      resolve: null,
      file_picker: useTemplateRef('file_picker'),
      id: '',
      listener: null,
      blob: true
    }
  },
  beforeMount() {
    this.id = 'file_picker_' + this.generateCode(24);
    $AF.$picker = ({accept, max, blob = true}) => {
      return new Promise(resolve => {
        this.max = max || 1;
        this.accept = accept || INPUT_FILE_TYPE.IMAGE;
        this.resolve = resolve;
        this.blob = blob;
        this.$nextTick(() => {
          this.file_picker.click();
        });
      });
    }
  },
  mounted() {

  },
  beforeDestroy() {

  },
  methods: {
    handleFileChange: function (e) {
      let clipboard = e.clipboardData || {};
      let clipboard_files = clipboard.files || clipboard.items;
      let files = (e.target || {}).files || (e.dataTransfer || {}).files || clipboard_files || [];
      // convert file to blob
      let result = [];
      for (let i = 0; i < files.length; i++) {
        let file = files[i];
        if (this.blob) {
          let blob = new Blob([file], {type: file.type, name: file.name});
          result.push(blob);
        }
        else{
          result.push(file);
        }
      }
      if (result.length > this.max) {
        this.$AF.$msg.error(`Bạn chỉ được chọn tối đa ${this.max} tệp`);
        return 0;
      } else {
        this.resolve && this.resolve(this.max === 1 ? result[0] : result);
      }
      this.file_picker.value = null;
    }
  },

}
</script>
<style lang="scss">
.file-picker {
  display: none;
}
</style>