<template>
  <label class="fn__toggle">
										<span class="t_in">
											<input type="checkbox"
                             v-model="scopeValue"
                             :disabled="disabled"
                      >
											<span class="t_slider"></span>
											<span class="t_content"></span>
										</span>
    <slot></slot>
  </label>
</template>
<script>
export default {
  name: 'component__switch',
  props: {
    modelValue: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      scopeValue: false,
    }
  },
  watch: {
    modelValue: function (val) {
      if (val !== this.scopeValue) {
        this.scopeValue = val;
      }
    },
    scopeValue: function (val) {
      if (val !== this.modelValue) {
        this.$emit('update:modelValue', val);
      }
    }
  },
  beforeMount() {
    this.scopeValue = this.modelValue;
  }
}
</script>