<template>
  <div class="fn__range">
    <div class="range_in">
      <input type="range"
             v-model="scopeValue"
             :min="min"
             :max="max"
      >
      <div class="slider"></div>
    </div>
    <div class="value">{{ scopeValue }}</div>
  </div>
</template>
<script>
export default {
  name: 'component__slider',
  props: {
    modelValue: {
      type: Number,
      default: 0
    },
    min: {
      type: Number,
      default: 0
    },
    max: {
      type: Number,
      default: 100
    }
  },
  data() {
    return {
      scopeValue: this.modelValue,
    }
  },
  watch: {
    modelValue: function (val) {
      if (val !== this.scopeValue) {
        this.scopeValue = val;
      }
    },
    scopeValue: function (val) {
      if (val !== this.modelValue) {
        this.$emit('update:modelValue', val);
      }
    }
  },
}
</script>
