<template>
  <select v-model="scopeValue" :id="id">
    <template v-for="e in options">
      <option :value="e[0]">{{ e[1] }}</option>
    </template>
  </select>
</template>
<script>
export default {
  name: 'component__select-single',
  props: {
    modelValue: {
      type: String,
      default: ''
    },
    options: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      scopeValue: '',
      id: '',
    }
  },
  watch: {
    modelValue: function (val) {
      if (val !== this.scopeValue) {
        this.scopeValue = false;
      }
    },
    scopeValue: function (val) {
      if (val !== this.modelValue) {
        this.$emit('update:modelValue', val);
      }
    }
  },
  beforeMount() {
    this.id = `select-single-${this.generateCode(24)}`
    this.scopeValue = this.modelValue
  }
}
</script>